:root {
  --color-dark-text: #494450;
  --color-text: #615c69;
  --color-light-text: #d2bea6;
  --font-bebas: 'Bebas Neue', Arial, Helvetica, sans-serif;
  --bg-body: #f1e2cf;
  --bg-light: #e8d7c3;
  --bg-dark: #494450;
}

@font-face {
  font-family: 'Bebas Neue';
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  src: url(../fonts/BebasNeueBold.woff2) format('woff2'), url(../fonts/BebasNeueBold.woff) format('woff');
}

@font-face {
  font-family: 'Montserrat';
  font-weight: 900;
  font-style: normal;
  font-display: swap;
  src: url(../fonts/Montserrat-Black.woff2) format('woff2'), url(../fonts/Montserrat-Black.woff) format('woff');
}

@font-face {
  font-family: 'Montserrat';
  font-weight: 500;
  font-style: normal;
  font-display: swap;
  src: url(../fonts/Montserrat-Medium.woff2) format('woff2'), url(../fonts/Montserrat-Medium.woff) format('woff');
}

html {
  height: 100%;
  line-height: 1.15;
  -webkit-text-size-adjust: 100%;
}

body {
  display: flex;
  flex-direction: column;
  min-height: 100%;
  margin: 0;
  font-weight: 500;
  font-size: 13px;
  font-family: 'Montserrat', Arial, Helvetica, sans-serif;
  color: var(--color-text);
  background-color: var(--bg-body);
}

abbr[title] {
  text-decoration: underline;
  text-decoration: underline dotted;
  border-bottom: none;
}

b,
strong {
  font-weight: bolder;
}

small {
  font-size: 80%;
}

img {
  max-width: 100%;
}

button,
input,
optgroup,
select,
textarea {
  margin: 0;
  font-size: 100%;
  line-height: 1.15;
  font-family: inherit;
}

button,
select {
  text-transform: none;
}

button,
[type="button"],
[type="reset"],
[type="submit"] {
  -webkit-appearance: button;
}

button::-moz-focus-inner,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
  padding: 0;
  border-style: none;
}

button:-moz-focusring,
[type="button"]:-moz-focusring,
[type="reset"]:-moz-focusring,
[type="submit"]:-moz-focusring {
  outline: 1px dotted ButtonText;
}

fieldset {
  padding: 0.35em 0.75em 0.625em;
}

legend {
  display: table;
  box-sizing: border-box;
  max-width: 100%;
  padding: 0;
  color: inherit;
  white-space: normal;
}

[type="number"]::-webkit-inner-spin-button,
[type="number"]::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  outline-offset: -2px;
  -webkit-appearance: textfield;
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  font: inherit;
  -webkit-appearance: button;
}

details {
  display: block;
}

summary {
  display: list-item;
}

.visually-hidden {
  position: absolute;
  width: 0;
  height: 0;
  margin: 0;
  padding: 0;
  overflow: hidden;
  border: none;
}

.col {
  padding-right: 30px;
  padding-left: 30px;
}

.btn {
  display: inline-block;
  padding: 15px 50px 12px;
  font-weight: 700;
  font-size: 22px;
  line-height: 1;
  font-family: var(--font-bebas);
  color: var(--color-light-text);
  text-transform: uppercase;
  text-decoration: none;
  letter-spacing: 3px;
  background-color: var(--bg-body);
  border: 0;
  border: 1px solid #d1bda5;
  border-radius: 25px;
  transition: box-shadow 0.3s ease-in-out;
  appearance: none;
}

.btn:hover {
  color: var(--color-light-text);
  box-shadow: 0px 4px 20px 0px var(--color-light-text);
}

.btn:focus,
.btn:active {
  color: var(--color-light-text);
  background-color: var(--bg-dark);
  border: 1px solid #d1bda5;
  outline-width: 2px;
  outline-style: double;
  outline-color: #d1bda5;
  outline-offset: -1px;
}

.btn-success {
  color: var(--color-light-text);
  background-color: var(--bg-dark);
  border: 1px solid var(--bg-dark);
  box-shadow: 0px 4px 16px 0px #3a2b4f;
  transition: box-shadow 0.3s ease-in-out;
}

.btn-success:hover {
  box-shadow: 0px 2px 4px 0px #3a2b4f;
}

.btn-success:focus,
.btn-success:active {
  outline-width: 2px;
  outline-style: double;
  outline-color: #d1bda5;
  outline-offset: -1px;
}

.btn:before {
  content: "";
  display: inline-block;
  height: 100%;
  vertical-align: middle;
}

.btn {
  display: inline-block;
  vertical-align: middle;
}

.form__fieldset {
  margin: 0;
  padding: 0;
  border: 0;
}

.form__legend {
  font-weight: 700;
  font-size: 22px;
  line-height: 45px;
  font-family: var(--font-bebas);
  color: var(--color-light-text);
  letter-spacing: 3px;
}

.form__grid .form__label {
  grid-row-end: 2;
  grid-row-start: 1;
}

.form__label {
  display: block;
  font-weight: 500;
  font-size: 13px;
  line-height: 20px;
  color: var(--color-dark-text);
}

.form__control {
  box-sizing: border-box;
  width: 100%;
  margin-bottom: 24px;
  padding: 16px 20px;
  font-weight: 900;
  font-size: 14px;
  line-height: 1;
  color: var(--color-dark-text);
  letter-spacing: 0.5px;
  background-color: var(--bg-light);
  border: 0;
  border: 1px solid var(--bg-light);
  border-radius: 6px;
  transition: box-shadow 0.3s ease-in-out;
}

.form__control:hover {
  box-shadow: 0px 4px 20px 0px var(--color-light-text);
}

.form__control:focus {
  outline-width: 2px;
  outline-style: double;
  outline-color: #d1bda5;
  outline-offset: -1px;
  box-shadow: 0px 4px 20px 0px var(--color-light-text);
}

body {
  align-items: center;
  padding: 50px 30px;
}

.blank {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  max-width: 520px;
}

.form {
  width: 100%;
}

@media (min-width: 576px) {
  .hidde-min-sm {
    display: none;
  }

  .form__control {
    font-size: 16px;
  }
}

@media (min-width: 768px) {
  .hidde-min-md {
    display: none;
  }

  .col {
    padding-right: 40px;
    padding-left: 40px;
  }

  .form__grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
    gap: 0 20px;
  }
}

@media (min-width: 990px) {
  .hidde-min-lg {
    display: none;
  }
}

@media (min-width: 1200px) {
  .hidde-min-xl {
    display: none;
  }
}

@media (min-width: 1620px) {
  .hidde-min-xxl {
    display: none;
  }
}

@media (max-width: 1619px) {
  .hidde-max-xxl {
    display: none;
  }
}

@media (max-width: 1199px) {
  .hidde-max-xl {
    display: none;
  }
}

@media (max-width: 989px) {
  .hidde-max-lg {
    display: none;
  }
}

@media (max-width: 767px) {
  .hidde-max-md {
    display: none;
  }
}

@media (max-width: 575px) {
  .hidde-max-sm {
    display: none;
  }
}